body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    position: relative;
    overflow-x: hidden;
    background: none; /* Ensure no background image is set */
}

.home-page {
    background-color: black; /* Set background color to black for the entire page */
    color: white;
}

.banner {
    position: relative;
    width: 100%;
    height: auto;
}

.banner img {
    width: 100%;
    height: auto;
    display: block;
}

.watch-trailer-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(180, 33, 112);
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    text-decoration: none; /* Remove underline */
    cursor: pointer;
    font-size: 16px;
}

.watch-trailer-button:hover {
    background-color: hotpink; /* Change color on hover if desired */
}

.container {
    position: relative;
    padding: 20px;
    /* Ensure no specific background set here, inherits black from .home-page */
}

.container h2 {
    margin: 20px 0;
    padding: 10px; /* Add padding for better readability */
    color: white; /* Set the category title text color to white */
    font-family: 'Courier New', Courier, monospace;
    font-weight: bolder;
    background-color: black; /* Set the background color of h2 to black */
}

.movie {
    background-color: black; /* Set background color to black */
    border-radius: 10px;
    box-shadow: none; /* Remove box shadow */
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 250px;
    padding: 0;
    margin: 0;
}

.movie:hover {
    transform: translateY(-10px);
    box-shadow: none; /* Remove box shadow */
}

.movie-poster {
    width: 100%;
    height: auto;
    max-height: 300px;
    border-radius: 10px;
    background: none; /* Ensure no background image is set */
}
