/* SingleMoviePage.css */

.single-movie-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #e25ea7;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center; /* Center align content */
}

.movie-title {
    font-size: 2.5rem;
    margin-bottom: 20px; /* Add space below the title */
}

.movie-poster {
    margin-bottom: 60px; /* Add space below the poster */
}

.movie-poster img {
    max-width: 100%;
    height: auto;
    max-height: 600px; /* Increase max-height for larger poster */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.movie-details {
    text-align: left; /* Align details to the left */
}

.movie-details p {
    margin-bottom: 10px;
    line-height: 1.6;
}

.overview {
    margin-top: 20px;
}

.overview h2 {
    font-size: 1.8rem;
    margin-bottom: 10px;
    
}

.overview p {
    line-height: 1.6;
}

